import {
  UPDATE_BREADCRUMB,
  LOGIN_SUCCESS,
  LOGIN_ERROR,
  THEME_CHANGE,
  ADD_MENU,
  DELETE_MENU
} from './mutation-types'
import * as actions from './actions'

const state = {
  pathList: [],
  user: null,

  // 
  themeColors: JSON.parse(window.localStorage.getItem('themeColors')) || {},

  // 
  menuList: [
    {
      name: '',
      icon: 'el-icon-fa-user',
      type: 'submenu',
      onOff: true,
      disabled: true,
      children: [
        {name: '', type: 'menu-item', index: '/user/list', route: {path: '/user/list'}},
        {name: '', type: 'menu-item', index: '/user/org', route: {path: '/user/org'}}
      ],
      desc: '',
      stateList: [
        {name: '', state: true},
        {name: '', state: true}
      ]
    },
    {
      name: '',
      icon: 'el-icon-fa-calendar-check-o',
      type: 'submenu',
      onOff: false,
      disabled: false,
      children: [],
      desc: 'APP',
      stateList: [
        {name: '', state: true}
      ]
    },
    {
      name: '',
      icon: 'el-icon-fa-jpy',
      type: 'submenu',
      onOff: false,
      disabled: false,
      children: [],
      desc: '',
      stateList: [
        {name: '', state: false},
        {name: '', state: false}
      ]
    },
    {
      name: '',
      icon: 'el-icon-fa-money',
      type: 'submenu',
      onOff: false,
      disabled: false,
      children: [],
      desc: '',
      stateList: [
        {name: '', state: false},
        {name: '/', state: false}
      ]
    },
    {
      name: '',
      icon: 'el-icon-fa-vcard-o',
      type: 'submenu',
      onOff: false,
      disabled: false,
      children: [],
      desc: '',
      stateList: [
        {name: '', state: false},
        {name: '', state: false}
      ]
    },
    {
      name: '',
      icon: 'el-icon-fa-unlock-alt',
      type: 'submenu',
      onOff: true,
      disabled: true,
      children: [
        {name: '', type: 'menu-item', index: '/setting', route: {path: '/setting'}}
      ],
      desc: '',
      stateList: [
        {name: '', state: false},
        {name: '', state: false}
      ]
    }
  ]
}

const mutations = {
  [UPDATE_BREADCRUMB] (state, pathList) {
    state.pathList = pathList
  },
  [LOGIN_SUCCESS] (state, user) {
    state.user = user
  },
  [LOGIN_ERROR] (state, user) {
    state.user = false
  },
  [THEME_CHANGE] (state, colors) {
    const themeColors = JSON.stringify(colors)
    state.themeColors = JSON.parse(themeColors)
    window.localStorage.setItem('themeColors', themeColors)
  },
  [ADD_MENU] (state, index) {
    state.menuList[index].onOff = true
  },
  [DELETE_MENU] (state, index) {
    state.menuList[index].onOff = false
  }
}

export default {
  state,
  mutations,
  actions
}



